.element__like-button {
  margin: 0 0 2px;
  width: 22px;
  height: 22px;
  outline: none;
  border: none;
  background: transparent url(../../../images/like.svg) center center / 22px no-repeat;
  transition: opacity .2s linear;
}

.element__like-button:hover {
  opacity: .5;
  cursor: pointer;
}